.address__inner {
  position: relative;
}

.address__item {
  margin-bottom: 1.9791666667vw;
  position: relative;
}
.address__item:last-child {
  margin-bottom: 0;
}

.address__title {
  font-size: 0.7291666667vw;
  font-weight: 400;
  line-height: 1.0416666667vw;
  letter-spacing: 0.08em;
  letter-spacing: normal;
  color: #b2b5bb;
  margin: 0 0 1.09375vw 0;
}
.address__title--first {
  margin: 0 0 41px 0;
}

.address__phone-link {
  display: inline-block;
  font-family: "Intro", "Arial", sans-serif;
  font-size: 1.5625vw;
  line-height: 2.1875vw;
  margin-bottom: 0.5208333333vw;
}

.address__data {
  font-size: 0.9375vw;
  line-height: 1.3020833333vw;
  color: #444b58;
}

.tooltip {
  position: absolute;
  left: 28%;
  top: 0;
}

.tooltip__button {
  display: inline-block;
  width: 1.0416666667vw;
  height: 1.0416666667vw;
  border-radius: 50%;
  border: none;
  background: url("../img/svg/question.svg") 50% 50%/50% no-repeat, linear-gradient(#ffffff);
  box-shadow: 0 0 0.5208333333vw rgba(255, 255, 255, 0.5);
  cursor: pointer;
}

.tooltip__text {
  display: none;
  position: absolute;
  bottom: 140%;
  left: -90%;
  margin: 0;
  background-color: #ffffff;
  box-shadow: 0 0 0.5208333333vw rgba(255, 255, 255, 0.5);
  width: 23.9583333333vw;
  padding: 0.5208333333vw 0.8333333333vw;
  border-radius: 0.2083333333vw;
}
.tooltip__text::before {
  content: "";
  position: absolute;
  top: 100%;
  left: 4%;
  border: 9px solid transparent;
  border-top: 9px solid #ffffff;
}

.tooltip__button:hover + .tooltip__text {
  display: block;
}

.socials {
  list-style-type: none;
  margin: 0;
  padding: 2.0833333333vw 0 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.socials__item {
  margin-right: 1.0416666667vw;
}
.socials__item:last-child {
  margin-right: 0;
}

.callback-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  padding: 3.8020833333vw 0 4.2708333333vw;
}

.callback-form {
  background-color: #484283;
  padding: 1.9791666667vw 0.8854166667vw;
  border-radius: 0.2083333333vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 32.2%;
}
.callback-form h2 {
  margin-bottom: 1.0416666667vw;
}
.callback-form p {
  line-height: 0.8333333333vw;
  color: #ffffff;
  width: 67%;
  text-align: center;
  margin: 0 0 2.0833333333vw 0;
}
.callback-form fieldset {
  margin-bottom: 0.3645833333vw;
  padding: 0;
}
.callback-form input {
  margin-bottom: 0.46875vw;
  padding: 0.9895833333vw;
  border-radius: 0.2083333333vw;
  border: none;
  width: 100%;
  line-height: 1.1458333333vw;
}
.callback-form input:last-of-type {
  margin: 0;
}
.callback-form input::placeholder {
  color: #b2b5bb;
}
.callback-form button {
  padding: 1.1458333333vw;
  border-radius: 0.2083333333vw;
  border: none;
  width: 98%;
  background-color: #f14f4f;
  color: #ffffff;
  font-family: "Intro", "Arial", sans-serif;
  line-height: 0.8333333333vw;
}

.instagram {
  padding: 0 3.125vw 0 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.instagram-title {
  text-align: center;
  margin: -0.625vw 0.5208333333vw 0.8333333333vw 0;
}

.instagram__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 8.1770833333vw 17.2395833333vw 8.1770833333vw;
  grid-template-rows: repeat(2, 8.3854166667vw);
  grid-column-gap: 0.9375vw;
  grid-row-gap: 0.6770833333vw;
  grid-auto-flow: row;
}

.instagram__item:nth-child(5n-3) {
  grid-row: span 2;
}

.instagram__photo {
  height: 100%;
}

.instagram__image {
  display: block;
  border-radius: 0.2083333333vw;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-footer {
  background-color: #444b58;
}

